import $ from 'jquery';
import apiDataFilter from '@/libraries/apiDataFilter';
export default {
	install(Vue,options){
		let env = apiDataFilter.getEnv();
		let apiPrefix = "//wechat.wkzf.com/";
		if (env === "test") apiPrefix = "//10.0.18.79:8134/" ;
    	else if (env === "beta") apiPrefix = "//wechat-beta.wkzf.com/" ;
    	else if (env === "sim") apiPrefix = "//wechat.sim.wkzf/" ;
    	else if (env === "prod") apiPrefix = "//wechat.wkzf.com/" ;
    	let apiUrl = apiPrefix + "wx_js_sdk_sign.rest?wechatCode=1000021";

    	let initial = true;
    	wx.ready(function() {
    		wx.showOptionMenu();	                    		
	    });

    	/*
			options:{
				timelineTitle:"",
				linkUrl:"",
				imgUrl:"",
				title:"",
				content:"",
				trigger:function(){
	
				},
				success:function(){
	
				},
				fail:function(){
	
				},
				complete:function(){
	
				}
			}
    	*/
		Vue.wechatShare = Vue.prototype.$wechatShare = function(options){			
			$.ajax({
	        	url : apiUrl ,
	        	type : "GET" ,
	        	data : { requestUrl : window.location.href } ,
	        	dataType : "jsonp" ,        
		        error : function(e) {
		            //console.log("error:" + e);
		        } ,
	        	success : function(result) {	        		
	            	var data = result.data ;
		            wx.config({
		                debug : true , // ,apialertpclogpc
		                appId : data.appid , // corpid
		                timestamp : data.timestamp , // 
		                nonceStr : data.nonce_str , // 
		                signature : data.signature , // 1
		                jsApiList : ['onMenuShareAppMessage', 'onMenuShareTimeline','hideMenuItems'] // JSJS2
		            }) ;
		            if(options.share){
						wx.ready(function() {
    		              wx.hideMenuItems({
                        	menuList: ["menuItem:share:appMessage","menuItem:share:timeline"] // menu3
                      	  }); 	                    		
	                    });
					}else{
						wx.ready(function(){
							console.log(options.linkUrl);
							console.log(options);
		            		wx.onMenuShareTimeline({
					        	title : options.timelineTitle || window.document.title,
					        	// link : options.linkUrl|| window.location.href,
					        	imgUrl : "https://cdn.wkzf.com/wkwap_fe/img/source/down/logo.jpg" ,
					        	//trigger : options.trigger ,
					        	//success : options.success ,
					        	//cancel : options.cancel ,
					        	//fail : options.fail,
					        	//complete:options.complete
					    	});

					    	wx.onMenuShareAppMessage({
					        	// title : options.title || window.document.title, // 
					        	//desc : options.content , // 
					        	// link : options.linkUrl || window.location.href, // 
					        	imgUrl : options.imgUrl  // 
					        	//type : '' , // ,musicvideolinklink
					        	//dataUrl : '' , // typemusicvideo
					        	//trigger : options.trigger ,
					        	//success : options.success ,
					        	//cancel : options.cancel ,
					        	//fail : options.fail,
					        	//complete:options.complete
					    	});
		            	});
					}
		            		            
	        	}
	    	});
		};
	}
};



